import { Injectable } from '@angular/core';
import { environment } from '../../../environments/environment';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class ResetPasswordService {
  api_url = environment.api_url;
  constructor(private http: HttpClient) { }

  async sendVerificationCode(postData){
    return await this.http.post(this.api_url + '/users/send_verification_code_password', postData).toPromise();
  }

  async verifyResetCode(postData){
    return await this.http.post(this.api_url + '/users/verify_password_reset_code', postData).toPromise();
  }

  async resetPassword(postData){
    return await this.http.post(this.api_url + '/users/reset_password', postData).toPromise();
  }

}
